* {
  margin: 0;
  padding: 0;
}

.profile {
  background-color: #f0f0f0;
  color: black;
  display: block;
  margin: 20px auto;
  width: 200px;
  border-radius: 5px;
  -webkit-transition: -webkit-transform 0.2s;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

.profile:hover {
  -webkit-box-shadow: 10px 5px 5px rgba(0, 0, 0, 0.4);
          box-shadow: 10px 5px 5px rgba(0, 0, 0, 0.4);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.profile .description {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.profile .description .avatar {
  width: 80px;
  border-radius: 50%;
  margin: 0 auto;
  margin-top: 25px;
}

.profile .description .name {
  font-weight: 700;
  margin: 15px auto;
}

.profile .description .tag,
.profile .description .location {
  color: grey;
  margin: 3px auto;
  font-size: 12px;
}

.profile .stats {
  border-top: solid 1px rgba(128, 128, 128, 0.4);
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: stretch;
      -ms-flex-align: stretch;
          align-items: stretch;
  margin: 20px 0px;
}

.profile .stats li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 12px;
  width: calc(100% / 3);
  padding: 15px 0;
}

.profile .stats li span {
  margin: 0 auto;
}

.profile .stats li span.label {
  color: grey;
}

.profile .stats li span.quantity {
  font-weight: 700;
}

.profile .stats li:first-child {
  border-right: solid 1px rgba(128, 128, 128, 0.4);
}

.profile .stats li:last-child {
  border-left: solid 1px rgba(128, 128, 128, 0.4);
}
